import React from 'react';
import { Link } from 'react-router-dom';
import Helmet from 'react-helmet';
import EntityContainer from 'modules/entity/containers';
import Typography from '@material-ui/core/Typography';
import Breadcrumbs from '@material-ui/core/Breadcrumbs';
import { ErrorBoundary } from 'shared/components';

import Skeleton from '@material-ui/lab/Skeleton';
import { useRouter } from 'shared/hooks';
import { useAppState } from 'shared/state';
import { CampaignEntity } from 'shared/typings/news.types';
import CreateNewsForm from '../../components/CreateNewsForm';

const CreateCampaignNews = () => {
	const { params } = useRouter();
	const campaignId = parseInt((params as { campaignId: string })?.campaignId);
	const { companyId } = useAppState();

	return (
		<EntityContainer.One
			id={campaignId}
			entity="campaigns"
			name={`Campaign-${campaignId}`}
			url={`/${companyId}/campaigns/${campaignId}/`}
			relations={''}
			params={{}}
		>
			{({ isFetched, item }) => {
				const campaignItem = item as CampaignEntity;
				return (
					<div className="news-page py-4">
						<Helmet>
							<title>KENNEKT | Create News</title>
							<meta name="description" content="KENNEKT Announcement" />
						</Helmet>
						<h1 className="mb-2 text-2xl font-bold">Create News</h1>
						<div className="mb-4">
							<Breadcrumbs aria-label="breadcrumb">
								{isFetched ? (
									<>
										<Link to={`/news/campaigns/view/${campaignId}`}>
											<span className="text-sm text-primary transition hover:text-primary-dark">
												{campaignItem.name}
											</span>
										</Link>
									</>
								) : (
									<div className="w-200">
										<Skeleton />
									</div>
								)}
								<Typography color="textPrimary" className="!text-sm">
									Create news
								</Typography>
							</Breadcrumbs>
						</div>
						<div className="uni-page__content">
							<CreateNewsForm />
						</div>
					</div>
				);
			}}
		</EntityContainer.One>
	);
};

export default function CreateCampaignNewsWithErrorBoundary(props: any) {
	return (
		<ErrorBoundary>
			<CreateCampaignNews {...props} />
		</ErrorBoundary>
	);
}
